<template>
  <div>
    <!-- <loader
      :isHide="getLoaderStatus"
      :description="getLoaderDescription"
    /> -->
    <div class="page" :class="disableBottomShadow">
      <div class="header" :style="headerStyle">
        <slot name="header"></slot>
      </div>
      <div class="body" :class="[disableBodyPadding, isFooterHidden]" :style="bodyStyle">
        <slot name="body"></slot>
      </div>
      <div class="footer">
        <slot name="footer"></slot>
      </div>
    </div>
    <!-- <modal
      v-if="getModalStatus"
      :name="getModalName"
    /> -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
// import Loader from '@/components/Loader';
// import Modal from '@/components/Modal';

export default {
  name: 'CreditSign',
  // components: {
  //   Loader,
  //   Modal,
  // },
  props: {
    title: {
      type: String,
      default: 'Кредитный лимит',
    },
    openModal: {
      type: Boolean,
      default: false,
    },
    isDisableBottomShadow: {
      type: Boolean,
      default: false,
    },
    isDisableBodyPadding: {
      type: Boolean,
      default: false,
    },
    isDisabledFooter: {
      type: Boolean,
      default: false,
    },
    headerStyle: {
      type: Object,
    },
    bodyStyle: {
      type: Object,
    },
  },
  methods: {
    goBack() {
      this.$router.back();
    },
  },
  computed: {
    ...mapGetters([
      'getModalStatus',
      'getModalName',
      'getLoader',
    ]),
    getLoaderStatus() {
      return this.getLoader.hide;
    },
    getLoaderDescription() {
      return this.getLoader.description;
    },
    disableBottomShadow() {
      return this.isDisableBottomShadow
        ? 'page__disabled-shadow'
        : '';
    },
    disableBodyPadding() {
      return this.isDisableBodyPadding
        ? 'body__disabled-padding'
        : '';
    },
    showModal() {
      return this.getModalStatus.openModal;
    },
    isHeaderDisabled() {
      console.log('this.$slots.header', this.$slots.header);
      return this.$slots.header
        ? ''
        : 'header__hidden';
    },
    isFooterDisabled() {
      console.log('this.$slots.footer', this.$slots.footer);
      return this.$slots.footer
        ? ''
        : 'footer__hidden';
    },
    isFooterHidden() {
      return this.isDisabledFooter
        ? 'body__footer-hidden'
        : '';
    },
  },
};
</script>

