<template>
  <div class="question-item">
    <div class="question" @click="handleToggleAnswer" :id="id">
      <img
        src="../../assets/promo-page/icon-add.svg"
        alt="icon-add"
        class="question__image"
        :class="{
        'question__image--rotate': textBlock.visible,
      }"
      />
      <span class="question-item__text">{{ textBlock.question }}</span>
    </div>
    <div
      class="answer"
      :class="{
        'answer--show': textBlock.visible,
      }"
    >
      <span class="answer__text">{{ textBlock.answer }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'QuestionItem',
  props: {
    textBlock: {
      type: Object,
      default: () => ({
        question: '',
        answer: '',
      }),
    },
    id: {
      type: Number,
    },
  },
  methods: {
    handleToggleAnswer(event) {
      this.$emit('toggle', Number(event.currentTarget.id));
    },
  },
};
</script>

