import Vue from 'vue';
import VueRouter from 'vue-router';
import DescriptionPage from '../pages/description-page';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'DescriptionPage',
    component: DescriptionPage,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: '',
  routes,
});

export default router;
