<template>
  <button
    class="base-button"
    :class="[genCssClass, getDisabledClass, classNames]"
    @click="handleClick"
    :disabled="isDisabled"
  >
    <slot />
  </button>
</template>

<script>
export default {
  name: 'BaseButton',
  props: {
    text: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: 'default',
    },
    isDisabled: {
      type: Boolean,
      default: false,
    },
    classNames: {
      type: String,
      default: '',
    },
  },
  methods: {
    handleClick() {
      this.$emit('click');
    },
  },
  computed: {
    genCssClass() {
      return this.type;
    },
    getDisabledClass() {
      return this.isDisabled
        ? 'disabled'
        : '';
    },
  },
};
</script>

